.button__remove {
    border: transparent;
    cursor: pointer;
    background: red;
    order: 2;
    min-width: 30px;
    min-height: 30px;
    color: white;
    transition: transform 0.25s;
  }
  @media (max-width: 600px) {
    .button__remove {
      transition: transform 0.5s;
    }
  }
  .button__remove:hover {
    transform: scale(1.1);
  }
  @media (max-width: 600px) {
    .button__remove:active {
      transform: scale(1.8);
    }
  }
  .button__done {
    border: transparent;
    cursor: pointer;
    background: green;
    min-width: 30px;
    min-height: 30px;
    order: -1;
    color: white;
    transition: transform 0.25s;
  }
  @media (max-width: 600px) {
    .button__done {
      transition: transform 0.5s;
    }
  }
  .button__done:hover {
    transform: scale(1.1);
  }
  @media (max-width: 600px) {
    .button__done:active {
      transform: scale(1.8);
    }
  }